.logo {
  height: 30px;
}

.navbar-brand {
  padding: 0;
}

.navbar > div {
  padding-top: 7px;
}

.card.login-widget,.card.setup-widget {
  margin-top: 130px;
}

.action-bar {
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.action-bar.borderless {
  border-bottom: none;
}

.action-bar .float-left .btn {
  margin-right: 10px;
}

.action-bar .float-right .btn {
  margin-left: 10px;
}

.dismiss-btn {
  padding-left: 2px;
  padding-right: 2px;
}

.navbar a.dropdown-toggle {
  color: #DDDDDD;
}
.navbar a.dropdown-toggle:hover {
  color: #FFFFFF;
}

.nav-tabs .nav-link.active {
  font-weight: bold;
}

.commit-list .list-group-item:nth-of-type(even) {
  background-color: #FBFBFB;
}

.commit-actions .btn, .branch-actions .btn, .run-commit {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: .8rem;
  vertical-align: center;
}

.lakefs-uri .octicon {
  color: #0c5460;
  margin-right: 5px;
}

.lakefs-uri a {
  color: #0f6674;
  text-decoration: underline;
}

.lakefs-uri {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: .9rem;
  color: #303030;
}

.lakefs-uri strong {
  margin: 0 5px;
}

/*  ref dropdown */
.ref-popover .popover-body {
  padding:0;
}

.ref-popover, .ref-popover .btn {
  font-size: 0.8rem;
  line-height: 0.8rem;
}

.ref-popover {
  max-width: 650px;
  min-width: 450px;
}

.ref-scroller {
  max-height: 250px;
  overflow: auto;
}

.ref-list .list-group-item:first-child {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.ref-list .list-group-item {
  padding: 10px 15px;
  border: 0;
  border-bottom: 1px solid #DDD;
  border-radius: 0;
  clear: both;
}
.ref-list .list-group-item .actions {
  float: right;
}
.ref-list .list-group-item .actions .badge {
  margin-right: 5px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.ref-popover h5 {
  padding: 10px;
  border-bottom: 1px solid #DDD;
  background-color: #FBFBFB;
  text-align: center;
}

.ref-filter-form {
  padding: 10px;
  border-bottom: 1px solid #DDD;
  background-color: #FBFBFB;
}
.ref-filter-form form {
  margin-bottom: 0;
}
.ref-paginator {
  text-align: center;
  padding: 10px 0;
}
/*  end ref dropdown */


/* tree table */
.diff-indicator {
  width: 50px;
}

.diff-indicator span {
  padding: 0 10px;
}

.table .tree-row {
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  height: 45px;
}

.table .tree-row td,
.actions-runs-list .table td {
  padding-top: 10px;
  padding-bottom: 10px;
  vertical-align: middle;
}

#run-details {
  margin-bottom: 20px;
}

.table .tree-row td:first-child {
  padding-left: 20px;
}

.table .tree-row td:nth-child(4) {
  padding-right: 20px;
  text-align: right;
}

.table .tree-row td:last-child {
  padding-right: 10px;
  text-align: right;
}

.tree-path .octicon {
  color: #007bff;
  margin-right: 5px;
  font-size: .9rem;
}

.diff-removed .tree-path, .diff-removed .tree-path .octicon, .diff-removed .diff-indicator .octicon {
  color: #808080;
}

.diff-changed {
  background-color: #d1ecf1;
}

.diff-added {
  background-color: #cdffd8;
}

.diff-removed {
  background-color: #ffdce0;
}

.diff-conflict {
  background-color: #fff3cd;
}

.table tr .row-hover {
  visibility: hidden;
  font-size: 0.7em;
}

.table tr:hover .row-hover {
  visibility: visible;
  display: inline-block;
}


.hook-logs {
  background: #222222;
  color: #FBFBFB;
  padding: 40px 10px;
}
.hook-log, .hook-log pre {
  background: #222222;
  color: #FBFBFB;
}
.hook-log pre {
  padding: 20px 0;
}
.hook-log-content {
  padding: 0 0 0 20px;
}
.hook-log-title {
  font-size: 1.2em;
}
.hook-log-title small {
  font-size: 0.7em;
  color: #AAAAAA;
  margin-left: 10px;
}

.hook-log-title .btn, .hook-log-title .btn.btn-link  {
    color: #FBFBFB;
}

.tree-row-actions {
  width: 30px;
  padding:0;
}

.table .tree-row .tree-row-actions .btn,
.actions-runs-list .table tbody tr td .clipboard-copy button,
.actions-runs-list .table tbody tr td .run-filter .link {
  visibility: hidden;
  font-size: 0.7rem;
}
.table .tree-row:hover .tree-row-actions .btn,
.table .tree-row:focus .tree-row-actions .btn,
.actions-runs-list .table tbody tr:hover td .clipboard-copy button ,
.actions-runs-list .table tbody tr:focus td .clipboard-copy button ,
.actions-runs-list .table tbody tr:hover td .run-filter .link ,
.actions-runs-list .table tbody tr:focus td .run-filter .link {
  visibility: visible;
  display: inline-block;
}

.tree-container .alert {
  /*margin: 20px;*/
}

.tree-container {
  margin-bottom: 30px;
  font-size: .9rem;
}

.tree-container .card .card-header {
  padding: 10px;
}

.tree-container .card .card-body {
  padding: 0;
}

.tree-container .card .card-body > .table {
  margin-bottom: 0;
}

.tree-container .btn.btn-link {
  font-size: .9rem;
}

.tree-container .table {
  margin-bottom: 0;
}

.paginator  {
  /*text-align: center;*/
}

.commit-metadata {
  font-size: 0.85rem;
}

.branch-table td {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.list-group.pagination-group {
  margin-bottom: 30px;
}

.policy-body {

  padding: 30px;
  background-color: #EEEEEE;
}


.auth-page .tab-content {
  padding: 20px 0;
}

textarea.form-control.policy-document {
  font-family: "SFMono-Regular", Menlo, Consolas, Monospace, serif;
  font-size: 10px;
  color: #e83e8c;
  background-color: #F8F8F8;
  padding: 10px;
}
textarea.form-control.policy-document:focus {
  font-family: "SFMono-Regular", Menlo, Consolas, Monospace, serif;
  font-size: 10px;
  color: #e83e8c;
  padding: 10px;
}

td .form-group {
  margin-bottom: 0;
}

.section-title {
  padding-bottom: 5px;
  padding-top: 10px;
  margin-bottom: 15px;
  border-bottom: 1px solid rgba(0,0,0,.125);
  overflow: hidden;
}

.btn {
  text-transform: none;
}
.btn.btn-link, .btn.btn-link:hover, .btn.btn-link:not(.disabled):hover {
  padding: 0;
  margin-top: 0;
  border-bottom-width: 0;
}
